import ClipboardJS from 'clipboard';
import axios from 'axios';

export default {
    name: 'Invite',
    mounted() {
        var clipboard = new ClipboardJS('.invitecode');
        clipboard.on('success', (e) => {
            e.clearSelection();
            // clipboard.destroy();
            this.$util.doClipboardCallback();
        });

        this.getUrl();
        this.getBanner();
        this.getUserInfo();
        setTimeout(() => {
            new Swiper('.swiper-container', {
                direction: 'horizontal',
                loop: false,
                slidesPerView: 1.5,
                initialSlide: 0,
                pagination: {
                    el: '.swiper-pagination',
                    type: 'bullets'
                }
            });
        });
    },
    data() {
        return {
            showBanner: true,
            userInfo: '',
            userId: '',
            banner: '',
            qrCode: '',
            invitecode: '',
            pictures: [
                require('../../image/i1-' + this.language + '.jpg'),
                require('../../image/i2-' + this.language + '.jpg'),
                require('../../image/i3-' + this.language + '.jpg'),
                require('../../image/i4-' + this.language + '.jpg'),
                require('../../image/i5-' + this.language + '.jpg')
            ],
            messages: {
                en: {
                    tweebaa: 'Tweebaa',
                    buttonText: 'Download',
                    downloadTips: 'Register now and earn a complimentary reward',
                    opportunity: 'Don’t miss out on an amazing opportunity',
                    myInviteCode: 'My invite code is',
                    myInviteCodeTips: 'Copy the invite code for sign up to get rewards of tweebucks',
                    platform: 'Tweebaa is the world’s 1st multi-dimensional value-exchange social networking platform. Designed for creating value from non-traditional products, ideas, and your personal influence; Tweebaa allows anyone to earn on a large scale with zero financial investment. With the tools we provide, anyone cancreate a personalized foundation for their business completely free and suggest, shop, or share products with people all over the world and earn money.',
                    story: 'Tweebaa is the world’s 1st multi-dimensional value-exchanging social networking platform. Designed for creating value from non-traditional products, ideas and your personal influence, Tweebaa enables everyone to achieve unlimited earnings with zero financial investment. With the tools we provide, anyone can create a personalized foundation, whether as a contributor to the social community, an influencer or a business, and it’s completely free. With Tweebaa you can earn simply by doing the things you do every day; liking, commenting, sharing, posting. You can also suggest, shop, sell or share physical and digital products with' +
                        'people all over the world to increase your earnings.'
                },
                zh: {
                    tweebaa: '推易吧',
                    buttonText: '下载',
                    downloadTips: '现在注册赚取更多奖励',
                    opportunity: '不要错过难得的机会',
                    myInviteCode: '我的邀请码是：',
                    myInviteCodeTips: '复制我的邀请码在注册时使用，使用邀请码注册会得到T豆奖励',
                    platform: '推易吧是一个让您利用自己的知识、经验、技巧、兴趣和人脉来变现的平台。推易吧会为您提供多样化的创意产品和工具，让您轻松开始零成本、零库存的创业之旅。',
                    story: '推易吧团队是一支综合技术型的创业团队。团队创始人王蕾是多维价值变现的成功实践者，是有着17年创新产品变现经验的专家。我们带着丰富的创新变现经验与底蕴踏入区块链时代。本着让“梦想补偿、职业替代”的理念，让普通人介入到垄断行业贡献并共享价值，我们努力着！'
                },
            }
        };
    },

    methods: {
        getBanner() {
            var i = Math.floor(Math.random() * 4) + 1;
            try {
                this.banner = require('../../image/banner' + i + '-' + this.$util.language + '.jpg');
            } catch (e) {

            }
        },
        getUrl() {
            var parameter = this.$util.getUrlParam('invitecode');
            var div = document.getElementById("code");
            div.dataset.clipboardText = parameter;
            this.invitecode = parameter;
        },
        getUserInfo() {
            var that = this;
            var url = this.$util.getBaseUrl() + "api/users/username/" + this.$util.getUrlParam('invitecode');
            this.$util.getAxois(url, "GET", {}).then(function (response) {
                // console.log(JSON.stringify(response));
                that.userInfo = response.data;
            }).catch(function (error) {
                console.log(JSON.stringify(error) + '#######################');
            });
        },
        copyCode() {
            document.getElementById("code").click();
            setTimeout(() => {
                if (this.$util.getPhoneSystem() === 'ios') {
                    window.location.href = 'https://apps.apple.com/us/app/tweebaa/id1467778439';
                } else if (this.$util.getPhoneSystem() === 'Android') {
                    window.location.href = 'weixin.html';
                }
            }, 1000)
        },
        close() {
            this.showBanner = false;
        }
    }
}
